export const mqtt = {
  data () {
    return {
      MQTT: require('mqtt')
    }
  },
  methods: {
    mqtt_connect () {
      if (typeof this.MQTT !== 'function' || this.$root.$data.mqtt.connected) return void 0

      this.$root.$data.mqtt.status.push(`connecting to: ${process.env.MQTT_PROTOCOL}://${this.$root.$data.mqtt.username}@${process.env.MQTT_HOST}:${process.env.MQTT_PORT}...`)
      var options = {
        username: this.$root.$data.mqtt.username,
        password: this.$root.$data.mqtt.password,
        useSSL: true,
        clientId: this.$root.$data.mqtt.username + '_' + Math.random().toString(16).substr(2, 8)
      }
      console.log(JSON.stringify(this.$root.$data.mqtt.username))
      this.$root.$data.mqtt.client = this.MQTT.connect(`${process.env.MQTT_PROTOCOL}://${process.env.MQTT_HOST}:${process.env.MQTT_PORT}`, options)
      this.$root.$data.mqtt.client.on('disconnect', this.onDisconnect)
      this.$root.$data.mqtt.client.on('connect', this.onConnect)
    },
    onDisconnect () {
      // overwritten by component methods
    },
    onConnect () {
      // overwritten by component methods
    }
  },
  mounted () {
    this.$root.$data.mqtt.status.push('connecting...')
    this.mqtt_connect()
  }
}



